.alpheios-popup {
  display: flex;
  flex-direction: column;
  background: #FFF;
  border: 1px solid lightgray;
  min-width: 210px;
  min-height: 150px;
  z-index: 1000;
  position: fixed;
  left: 200px;
  top: 100px;
  box-sizing: border-box;
  /* Required for Interact.js to take element size with paddings and work correctly */
  overflow: auto;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 16px;
  color: #666666; }

.alpheios-popup__header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  flex: 0 0 45px;
  padding: 10px 10px 0;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.alpheios-popup__header-text {
  line-height: 1;
  align-items: flex-start;
  padding: 7px 20px 0 0; }

.alpheios-popup__header-selection {
  font-size: 16px;
  font-weight: 700;
  color: #4E6476; }

.alpheios-popup__header-word {
  font-size: 12px;
  position: relative;
  top: -1px; }

.alpheios-popup__close-btn {
  display: block;
  position: absolute;
  width: 20px;
  right: 5px;
  top: 2px;
  cursor: pointer;
  fill: #D1D1D0;
  stroke: #D1D1D0;
  background: inherit;
  stroke-width: 2.5; }

.alpheios-popup__close-btn:hover,
.alpheios-popup__close-btn:focus {
  fill: #5BC8DC;
  stroke: #5BC8DC; }

.alpheios-popup__notifications {
  display: none;
  position: relative;
  padding: 10px 20px;
  background: #BCE5F0;
  flex: 0 0 60px;
  box-sizing: border-box;
  overflow: hidden; }

.alpheios-popup__notifications-close-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  display: block;
  width: 20px;
  height: 20px;
  margin: 0;
  cursor: pointer;
  fill: #D1D1D0;
  stroke: #D1D1D0; }

.alpheios-popup__notifications-close-btn:hover,
.alpheios-popup__notifications-close-btn:focus {
  fill: #5BC8DC;
  stroke: #5BC8DC; }

[data-notification-visible="true"] .alpheios-popup__notifications {
  display: block; }

.alpheios-popup__notifications--lang-switcher {
  font-size: 12px;
  float: right;
  margin: -20px 10px 0 0;
  display: inline-block; }

.alpheios-popup__notifications--lang-switcher .uk-select {
  width: 120px;
  height: 25px; }

.alpheios-popup__notifications--important {
  background: #73CDDE; }

.alpheios-popup__morph-cont {
  flex: 1 1;
  box-sizing: border-box;
  margin: 5px 10px 0;
  overflow: auto;
  padding: 10px;
  border: 1px solid #B8B7B5; }

.alpheios-popup__morph-cont-providers-header {
  display: inline-block;
  /* color: $alpheios-link-color;
    font-size: 0.75*$alpheios-base-font-size; */
  font-weight: 700;
  margin-top: 2px; }

.alpheios-popup__definitions--placeholder {
  border: 0 none;
  padding: 10px 0; }

img.alpheios-popup__logo {
  height: 16px;
  width: auto;
  vertical-align: middle; }

.alpheios-popup__more-btn {
  float: right;
  margin-bottom: 10px;
  /* font-size: 0.675 * $alpheios-base-font-size; */ }

.alpheios-popup__morph-cont-providers-source {
  font-size: smaller;
  font-weight: normal;
  /* color: $alpheios-toolbar-color; */
  font-style: italic;
  margin-left: .5em;
  margin-top: .5em;
  max-width: 700px; }

.alpheios-popup__providers {
  margin: 0 0 5px 10px; }

/*
.alpheios-popup__providers-link {
  font-size: 0.675*$alpheios-base-font-size;
}*/
.alpheios-popup__providers-link {
  display: inline-block;
  vertical-align: middle;
  padding: 5px 0 0; }
